import React              from 'react'
import { graphql }        from 'gatsby'
import { useStaticQuery } from 'gatsby'
import { SocialIcon }     from 'react-social-icons'
import styles             from './footer.module.scss'

const Footer = () => {
    const data = useStaticQuery(graphql`
    query {
        site {
          siteMetadata {
            author
          }
        }
      }
    `)
    return (
        <footer>

          <div>
            &copy; 2020 {data.site.siteMetadata.author}
          </div>

          <div>
            <div className={styles.socialIcon}> <SocialIcon bgColor='white' fgColor='black' style={{ height: 25, width: 25 }} url='http://github.com/in/daspeks' /> </div>
            <div className={styles.socialIcon}> <SocialIcon bgColor='white' fgColor='black' style={{ height: 25, width: 25 }} url='http://instagram.com/daspeks' /> </div>
            <div className={styles.socialIcon}> <SocialIcon bgColor='white' fgColor='black' style={{ height: 25, width: 25 }} url='http://twitter.com/daspeks' /> </div>
          </div>
          
        </footer>
    )
}

export default Footer